import React from 'react';
import {Panel,PanelHeader,PanelHeaderBack,Group,Header,Div} from '@vkontakte/vkui';

import styles from "../styles/custom-styles.sass";
import { RanksItem } from '../components';

const RankInfo = ({ ranks, go, id }) => {

    const renderRanks = ranks.map(item => {
        return (
            <RanksItem
                key={item.id}
                id={item.id}
                name={item.name}
                ratingMin={item.ratingMin}
                ratingMax={item.ratingMax}
                pillBlue={item.pillBlue}
                pillRed={item.pillRed}
                pillGold={item.pillGold}
                shirt={item.shirt}
                cell={item.cell}
                vacation={item.vacation}
            />
        );
    });



    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={go} data-to="home" />}>
                Звания в ДУРКЕ
            </PanelHeader>

            <Div>
                <Group header={<Header mode="secondary" aside="Рейтинг">Звание</Header>}>
                </Group>
                {renderRanks}
            </Div>

        </Panel>
    );
};

export { RankInfo };
