import React from 'react';
import { useState, useEffect } from 'react';
import {
    Panel, PanelHeader, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div, Spinner
} from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';


import styles from "../styles/custom-styles.sass";
import { ListUsers, ListGroups } from '../components';
import requestMethod from '../helpers/requestMethod';
import { getRankByRating } from '../helpers/getRankByRating';

const Top = ({ ranks, go, id, userPlace, rating, goHome, groupsTop, globalTop, vk }) => {
    const [userToken, setUserToken] = useState(null);
    const [activeTab, setActiveTab] = useState('general');
    const [friendsTop, setFriendsTop] = useState(null);
    const [renderFriends, setRenderFriends] = useState([]);

    function tabGeneral() {
        setActiveTab('general');
        window.scrollTo(0, 0);
    }

    function tabFriends() {
        setActiveTab('friends');
        getFriendsAccess();
        window.scrollTo(0, 0);
    }

    function tabGroups() {
        setActiveTab('groups');
        window.scrollTo(0, 0);
    }

    function getFriendsAccess() {
        bridge.sendPromise('VKWebAppGetAuthToken', {
            "scope": "friends",
            "app_id": 7351258
        }).then(res => {
            console.log(res);
            if (res.access_token) {
                setUserToken(res.access_token);
                getAndSendToServerFriends(res.access_token);
            } else {
                // TODO: Сделать ошибку о том, что пользователь не предоставил доступ к друзьям
            }
        }).catch(err => {
            vk.log(err);
        });
    }

    function getAndSendToServerFriends(userToken) {
        bridge.sendPromise('VKWebAppCallAPIMethod', {
            "method": "friends.get",
            "params": {
                "access_token": userToken,
                "v": "5.103"
            }
        }).then(res => {
            console.log("res", res);
            if (res.response) {
                axios.post(`${requestMethod}/getFriendsTop`, {
                    items: res.response.items,
                    search: document.location.search
                }).then(({ data: { message } }) => {
                    if (!message.length) {
                        return setFriendsTop('У Вас нет друзей в игре');
                    }

                    setFriendsTop(true);
                    setRenderFriends(message.map((item, index) => {
                        return (
                            <ListUsers
                                key={index}
                                id={index + 1}
                                idVk={item.id}
                                photoURL={item.photoURL}
                                firstName={item.firstName}
                                lastName={item.lastName}
                                rating={item.rating}
                                amount={item.durkaCoins}
                                ranks={ranks || []}
                            />
                        );
                    }));
                });
            } else {
                return setFriendsTop('Ошибка при загрузке друзей');
            }
        }).catch(err => {
            return setFriendsTop('Ошибка при загрузке друзей');
        });
    }

    const renderGlobal = globalTop.map((item, index) => {
        return (
            <ListUsers
                key={index}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.durkaCoins}
                ranks={ranks || []}
            />
        );
    });

    const renderGroups = groupsTop.map((item, index) => {
        return (
            <ListGroups
                key={index}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                name={item.name}
                rating={item.rating}
            />
        );
    });

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Топ
            </PanelHeader>

            <FixedLayout vertical="top" filled={true}>
                <Tabs >
                    <TabsItem
                        onClick={tabGeneral}
                        selected={activeTab === 'general'}
                    >
                        Общий
                    </TabsItem>
                    <TabsItem
                        onClick={tabFriends}
                        selected={activeTab === 'friends'}
                    >
                        Друзья
                    </TabsItem>
                    <TabsItem
                        onClick={tabGroups}
                        selected={activeTab === 'groups'}
                    >
                        Сообщества
                    </TabsItem>
                </Tabs>
            </FixedLayout>


            {activeTab === 'general'
                ?
                <>
                    {renderGlobal.length
                        ?
                        <>
                            <Div className="headerTop">
                                Ваше место: {userPlace}
                                <>
                                    &#12288;{`Ваш Рейтинг:`}
                                    <Icon16Fire className="SUKA" fill="red" />
                                    {rating}
                                </>
                            </Div>

                            <Div>
                                {renderGlobal}
                            </Div>
                        </>
                        :
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                            <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                    }
                </>
                :
                ''
            }


            {
                activeTab === 'friends'
                    ?
                    <div style={{ marginTop: 40 }}>
                        {friendsTop === null
                            ?
                            <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                                <Spinner size="large" style={{ marginTop: 90 }} />
                            </div>
                            :
                            (
                                !renderFriends.length
                                    ?
                                    <Div>
                                       {friendsTop}
                                    </Div>
                                    :
                                    <Div>
                                        {renderFriends}
                                    </Div>
                            )
                        }
                    </div>
                    :
                    ''
            }


                {activeTab === 'groups'
                ?
                <div style={{ marginTop: 40 }}>
                    {renderGroups.length
                        ?
                            <Div>
                                {renderGroups}
                            </Div>
                        :
                        <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                            <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                    }
                </div>
                :
                ''
            }

        </Panel >
    );
};

export { Top };
