import React from 'react';
import { Cell, Div, Link } from '@vkontakte/vkui';

const Inventory = ({ id, name, color, price, icon, openPay, openInfo, transfer, buyItem,
    openUseItem, count, $useItem, targetIdInventory, scheme }) => {

    return (
        <Div className={scheme === "bright_light" ? "cardShopInventoryBlack" : "cardShopInventoryGray"}>
            <Cell
                className='myItem'
                before={icon}
                asideContent={
                    <Link
                        id={id}
                        mode="tertiary">
                        {count} шт
                    </Link>
                }
                description={
                    <Div className="div">
                        <Link id={id} onClick={openUseItem}>
                            Использовать
                            </Link>
                    </Div>
                }
            >
                {name}
            </Cell>
        </Div>
    );
};

export { Inventory };

