import React, { useState, useEffect } from 'react';
import { Cell, Div, Avatar, Link, } from '@vkontakte/vkui';
import Icon16Down from '@vkontakte/icons/dist/16/down';
import Icon16Up from '@vkontakte/icons/dist/16/up';
import styles from "../styles/custom-styles.sass";

const OperationsHistoryItem = ({ senderId, targetId, amount, createdAt, formatDate, fetchedUser, senderName,
    targetName, senderPhoto, targetPhoto }) => {
    const photoURLTargetId = 'https://vk.com/id' + targetId;
    const photoURLSenderId = 'https://vk.com/id' + senderId;
    const idVkTargetId = 'https://vk.com/id' + targetId
    let now = new Date(createdAt);

    console.log("OperationsHistoryItem photoURLTargetId", photoURLSenderId);
    console.log("OperationsHistoryItem fetchedUser", fetchedUser);



    return (
        <>
            {senderId === fetchedUser.id
                ?
                <Div className='card'>
                    <span className='listNumber'></span>
                    <Cell
                        className='myItem'
                        before={
                            fetchedUser.photo_200
                                ?
                                <Link href={photoURLTargetId} target="_blank">
                                    <Avatar src={targetPhoto} />
                                </Link>
                                :
                                <Avatar src={'https://vk.com/images/camera_200.png'} />
                        }

                        description={
                            <Div style={{ marginLeft: 20 }} className="div">
                                <Div className="div">
                                    {fetchedUser &&
                                        <Div className="div">
                                            {targetName}
                                        </Div>
                                    }

                                </Div>
                                <Div className="div" style={{color: "red" }}>
                                    DurkaCoins: {amount}
                                </Div>
                            </Div>
                        }
                        asideContent={
                            <Div className="divFormatDate">
                                {formatDate(now)}
                            </Div>
                        }
                    // before={<Icon24FavoriteOutline/>}
                    // indicator={rating}
                    >
                        <Div className="divOperationsHistory">
                            <Div className="div">
                                <Icon16Up fill="red" />
                            </Div>
                            <Div className="div" style={{ marginTop: 4,marginLeft: 4 }}>
                                Перевод от
                            </Div>
                        </Div>
                    </Cell>

                </Div>
                :
                < Div className='card'>
                    <span className='listNumber'></span>
                    <Cell
                        className='myItem'
                        before={
                            senderPhoto
                                ?
                                <Link href={photoURLSenderId} target="_blank">
                                    <Avatar src={senderPhoto} />
                                </Link>
                                :
                                <Avatar src={'https://vk.com/images/camera_200.png'} />
                        }

                        description={
                            <Div style={{ marginLeft: 20 }} className="div">
                                <Div className="div">
                                        <Div className="div">
                                            {senderName}
                                        </Div>

                                </Div>
                                <Div className="div" style={{color: "green" }}>
                                    DurkaCoins: {amount}
                                </Div>
                            </Div>
                        }
                        asideContent={
                            <Div className="divFormatDate">
                                {formatDate(now)}
                            </Div>
                        }
                    // before={<Icon24FavoriteOutline/>}
                    // indicator={rating}
                    >
                        <Div className="divOperationsHistory">
                            <Div className="div">
                                <Icon16Down fill="green" />
                            </Div>
                            <Div className="div">
                                Пополнение от
                        </Div>
                        </Div>
                    </Cell>

                </Div>
            }
        </>
    );
};

export { OperationsHistoryItem };

