import React from 'react';
import { useState, useEffect } from 'react';
import {
    Panel, PanelHeader, PanelHeaderBack,
    FixedLayout, Tabs, TabsItem, Div, Spinner
} from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import bridge from '@vkontakte/vk-bridge';
import axios from 'axios';


import styles from "../styles/custom-styles.sass";
import { OperationsHistoryItem } from '../components';
import requestMethod from '../helpers/requestMethod';
import { getRankByRating } from '../helpers/getRankByRating';

const OperationsHistory = ({ ranks, go, id, userPlace, rating, goHome, groupsTop, globalTop,
    vk, dataOperationsHistory, fetchedUser }) => {

    // const [fetchedUser, setUser] = useState(null);
    // useEffect(() => {
    //     bridge.send('VKWebAppGetUserInfo').then(setUser);
    // }, []);

    

    function formatDate(date) {

        let dd = date.getDate();
        if (dd < 10) dd = '0' + dd;
        let mm = date.getMonth() + 1;
        if (mm < 10) mm = '0' + mm;
        let yy = date.getFullYear() % 100;
        if (yy < 10) yy = '0' + yy;
        let hh = date.getHours();
        if (hh < 10) hh = '0' + hh;
        let minutes = date.getMinutes();
        if (minutes < 10) minutes = '0' + minutes;

        return dd + '.' + mm + '.' + yy + ' ' + hh + ':' + minutes;
    }

    // console.log("OperationsHistoryItem fetchedUser", fetchedUser);

    const renderOperationsHistoryItem = dataOperationsHistory.map((item, index) => {
        return (
            <OperationsHistoryItem
                key={index}
                senderId={item.senderId}
                targetId={item.targetId}
                amount={item.amount}
                createdAt={item.createdAt}
                senderName={item.senderName}
                targetName={item.targetName}
                senderPhoto={item.senderPhoto}
                targetPhoto={item.targetPhoto}
                formatDate={formatDate}
                fetchedUser={fetchedUser}
                
                
            />
        );
    });

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                История операций
            </PanelHeader>

            <Div>

                {renderOperationsHistoryItem}
            </Div>

        </Panel >
    );
};

export { OperationsHistory };
