import React from 'react';
import {Cell,Div,Avatar,Link,} from '@vkontakte/vkui';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';
import styles from "../styles/custom-styles.sass";
import { getRankByRating } from '../helpers';

const ListGroups = ({ idVk, id, name, rating, photoURL }) => {
    const urlUser = 'https://vk.com/club' + idVk;

    return (
        <Div className='card'>
            <span className='listNumber'>{id}</span>
            <Link href={urlUser} target="_blank">
                <Cell
                    className='myItem'
                    before={photoURL ? <Avatar src={photoURL} /> :
                        <Avatar src={'https://vk.com/images/camera_200.png'} />}
                    description={
                        <Div className="div">
                            <Div className="div">
                            <Icon12Fire fill="red"/>
                                 {`${" "}${rating} ${" "}`} 
                            </Div>
                        </Div>
                    }
                >
                    {name}
                </Cell>
            </Link>
        </Div>
    );
};

export { ListGroups };

